.wph-modal.wph-modal--cabriolet {
	header {
		min-height: 24px;
		position: relative;
		h2.wph-modal--title,
		h4.wph-modal--subtitle {
			color: $white;
			text-align: left;
			padding: 0 30px 0 0;
		}
		.wph-modal--close {
			position: absolute;
			right: 0;
			bottom: 0;
			margin-bottom: 0;
		}
	}
	section {
		display: flex;
		flex-wrap: wrap;
		background: $white;
		.wph-modal--image {
			flex: 0 316px;
			img {
				width: 316px;
				height: 100%;
			}
			&_full {
				flex: 1;
			}
		}
		.wph-modal--message {
			flex: 1;
			padding: 20px;
		}
		.wph-modal--clear {
			margin-top: 15px;
			&:first-child {
				margin-top: 0;
			}
			.wph-modal-never-see-again {
				max-width: 200px;
				display: block;
				margin-top: 10px;
				@media (min-width: 771px) {
					max-width: none;
					display: inline;
					margin-top: 0;
					margin-left: 20px;
				}
			}
		}
		// Image: Align to left
		&.wph-modal--image_left {
			flex-direction: row;
			@media (max-width: 770px){
				flex-direction: column;
			}
		}
		// Image: Align to right
		&.wph-modal--image_right {
			flex-direction: row-reverse;
			@media (max-width: 770px){
				flex-direction: column-reverse;
			}
		}
	}
	// Improve content for mobiles
	header {
		+ section .wph-modal--image {
			img {
				@media (max-width: 770px) {
					width: 100%;
					height: 120px;
				}
			}
			+ .wph-modal--message {
				@media (max-width: 770px) {
					max-height: 220px;
					flex: 1;
					overflow-y: auto;
				}
			}
			&.mobile-hidden {
				+ .wph-modal--message {
					@media (max-width: 770px) {
						flex: 0 270px;
						overflow-y: auto;
					}
				}
			}
			@media (max-width: 770px) {
				flex: 0 120px;
			}
		}
		&.wph-modal--nocontent + section .wph-modal--image {
			img {
				@media (max-width: 770px) {
					width: 100%;
					height: 120px;
				}
			}
			+ .wph-modal--message {
				@media (max-width: 770px) {
					flex: 0 250px;
					overflow-y: auto;
				}
			}
			&.mobile-hidden {
				+ .wph-modal--message {
					@media (max-width: 770px) {
						flex: 0 380px;
						overflow-y: auto;
					}
					@media (max-width: 770px) and (max-height: 400px) {
						flex: 0 200px;
					}
				}
			}
			@media (max-width: 770px) {
				flex: 0 120px;
			}
		}
	}
}

// Image: Border Radius
.wph-modal.wph-modal--cabriolet {
	.wph-modal--content {
		// Image: Left
		.wph-modal--image_left {
			.wph-modal--image,
			.wph-modal--image img {
				border-top-left-radius: inherit;
				@media (min-width: 771px) {
					border-bottom-left-radius: inherit;
				}
				@media (max-width: 770px) {
					border-top-right-radius: inherit;
				}
			}
		}
		// Image: Right
		.wph-modal--image_right {
			.wph-modal--image,
			.wph-modal--image img {
				border-bottom-right-radius: inherit;
				@media (min-width: 771px) {
					border-top-right-radius: inherit;
				}
				@media (max-width: 770px) {
					border-bottom-left-radius: inherit;
				}
			}
		}
		// Image: Full
		.wph-modal--image_left,
		.wph-modal--image_right {
			.wph-modal--image_full,
			.wph-modal--image_full img {
				border-radius: inherit;
			}
		}
	}
}