$color--white: #FFFFFF;

$color--facebook: #2D5F9A;
$color--twitter: #00C3F3;
$color--google: #D73D32;
$color--pinterest: #CD252B;
$color--reddit: #F05825;
$color--linkedin: #007BB5;
$color--vk: #537599;

.inc_social_sharing_shortcode_wrap {
	margin: 20px 0;
}

.wph-social-sharing {
	.wph-sshare--container {
		display: inline-flex;
		flex-wrap: wrap;
		&, * {
			box-sizing: border-box;
		}
		a {
			box-shadow: none;
			box-sizing: border-box;
			color: $color--white;
			font: 600 12px/14px "Roboto", sans-serif;
			text-decoration: none;
			text-align: center;
			&:hover, &:focus, &:active {
				color: inherit;
				box-shadow: none;
			}
		}
		&.wph-sshare--row {
			flex-direction: row;
			align-items: center;
			padding: 15px 20px;
			a {
				margin-right: 20px;
				&.native-social-share {
					border-color: transparent !important;
				}
				&:last-child {
					margin-right: 0;
				}
			}
		}
		&.wph-sshare--column {
			flex-direction: column;
			padding: 15px 21px;
			a {
				margin-bottom: 15px;
				&:last-child {
					margin-bottom: 0;
				}
			}
		}
		// Default Colors
		// Background, Fill and Icon Border
		&.wph-sshare--design_one,
		&.wph-sshare--design_two {
			.wph-social {
				border: 1px solid transparent;
				background: transparent;
			}
			.wph-facebook {
				border-color: $color--facebook;
				.wph-social-icon {
					fill: $color--facebook;
				}
			}
			.wph-twitter {
				border-color: $color--twitter;
				.wph-social-icon {
					fill: $color--twitter;
				}
			}
			.wph-google {
				border-color: $color--google;
				.wph-social-icon {
					fill: $color--google;
				}
			}
			.wph-pinterest {
				border-color: $color--pinterest;
				.wph-social-icon {
					fill: $color--pinterest;
				}
			}
			.wph-reddit {
				border-color: $color--reddit;
				.wph-social-icon {
					fill: $color--reddit;
				}
			}
			.wph-linkedin {
				border-color: $color--linkedin;
				.wph-social-icon {
					fill: $color--linkedin;
				}
			}
			.wph-vk {
				border-color: $color--vk;
				.wph-social-icon {
					fill: $color--vk;
				}
			}
		}
		&.wph-sshare--design_three,
		&.wph-sshare--design_four {
			.wph-social {
				path {
					fill: $color--white;
				}
			}
			.wph-facebook {
				background: $color--facebook;
			}
			.wph-twitter {
				background: $color--twitter;
			}
			.wph-google {
				background: $color--google;
			}
			.wph-pinterest {
				background: $color--pinterest;
			}
			.wph-reddit {
				background: $color--reddit;
			}
			.wph-linkedin {
				background: $color--linkedin;
			}
			.wph-vk {
				background: $color--vk;
			}
		}
		// Default Colors
		// Container Border
		&.wph-sshare--color_default a.native-social-share {
			&[data-social="facebook"] {
				border: 1px solid $color--facebook;
			}
			&[data-social="twitter"] {
				border: 1px solid $color--twitter;
			}
			&[data-social="google"] {
				border: 1px solid $color--google;
			}
			&[data-social="pinterest"] {
				border: 1px solid $color--pinterest;
			}
			&[data-social="reddit"] {
				border: 1px solid $color--reddit;
			}
			&[data-social="linkedin"] {
				border: 1px solid $color--linkedin;
			}
			&[data-social="vkontakte"] {
				border: 1px solid $color--vk;
			}
		}
		
	}
}