html.no-scroll {
    overflow-y: hidden;
}
html.no-scroll body {
	overflow: hidden;
}

// Overlay Mask
.wpoi-overlay-mask {
	position: fixed;
	z-index: 999;
	&.wpoi-animate {
		width: 100%;
		max-width: 100%;
		height: 100%;
		max-height: 100%;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		transform: translate(0,0);
	}
	.wpoi-popup-overlay {
		width: 100%;
		height: 100%;
		position: fixed;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
	}
}

.inc_optin {
	// NSA Message
	.wpoi-nsa {
		display: block;
		padding: 5px 20px;
		text-align: center;
		.inc_opt_never_see_again {
			display: inline-block;
			margin: 0;
			padding: 0;
			cursor: pointer;
			font: 600 12px/20px "Open Sans", sans-serif;
			transition: 0.15s ease;
		}
	}
}
.wpoi-success-close {
	float: right;
	cursor: pointer;
	width: 25px;
	height: 25px;
	background-color: rgba(0,0,0, 0.2);
	line-height: 1;
	margin: 8px;
	border-radius: 20px;

	.wpoi-icon.i-close.i-small {
		font-size: 12px;
		margin: 0;
		line-height: 20px;
	}
}
.inc_opt_popup,
.inc_opt_slidein {
	.wpoi-success-close {
		display: none;
	}
}

.wph-modal--content {
	audio,video,object,iframe {
		height: auto;
		max-width: 100%;
	}
}

// Social Sharing
.wph-social-sharing {
	&.wph-social-sharing-float {
		position: fixed;
        z-index: 99999;
		&.wph-ssharing-top_left {
			top: 0;
			left: 0;
		}
		&.wph-ssharing-top_right {
			top: 0;
			right: 0;
		}
		&.wph-ssharing-bottom_left {
			bottom: 0;
			left: 0;
		}
		&.wph-ssharing-bottom_right {
			bottom: 0;
			right: 0;
		}
	}
}