.wph-modal.wph-modal--simple {
	.wph-modal--content {
		display: flex;
		flex-wrap: wrap;
		background: $white;
		.wph-modal--image {
			flex: 0 316px;
			img {
				width: 316px;
				height: 100%;
				@media (max-width: 770px) {
					width: 100%;
					height: 120px;
				}
			}
			&_full {
				flex: 1;
				@media (max-width: 770px) {
					flex: 0 200px;
				}
			}
			@media (max-width: 770px) {
				flex: 0 120px;
			}
		}
		.wph-modal--wrap {
			flex: 1;
			padding: 20px;
			@media (max-width: 770px) {
				max-height: 250px;
				overflow-y: auto;
			}
		}
		header {
			padding-bottom: 20px;
			border-bottom: 1px solid rgba(0,0,0,0.08);
			h2.wph-modal--title,
			h4.wph-modal--subtitle {
				color: $black;
			}
			&.wph-modal--nocontent {
				margin: 0;
				padding-bottom: 0;
				border-bottom: 0;
			}
		}
		.wph-modal--clear {
			margin-top: 15px;
			a:not(:first-child) {
				margin-left: 20px;
			}
			a:not(.wph-modal--cta) {
				color: $green;
				&:hover {
					color: $green-dark;
				}
			}
		}
		// Image: Left
		&.wph-modal--image_left {
			flex-direction: row;
			@media (max-width: 770px) {
				flex-direction: column;
			}
		}
		// Image: Right
		&.wph-modal--image_right {
			flex-direction: row-reverse;
			@media (max-width: 770px) {
				flex-direction: column-reverse;
			}
		}
	}
}

// Image: Border Radius
.wph-modal.wph-modal--simple {
	.wph-modal--content {
		// Image: Left
		&.wph-modal--image_left {
			.wph-modal--image,
			.wph-modal--image img {
				border-top-right-radius: inherit;
				border-top-left-radius: inherit;
				@media (min-width: 771px) {
					border-top-right-radius: 0;
					border-bottom-left-radius: inherit;
				}
			}
		}
		// Image: Right
		&.wph-modal--image_right {
			.wph-modal--image,
			.wph-modal--image img {
				border-top-right-radius: 0;
				border-bottom-right-radius: inherit;
				border-bottom-left-radius: inherit;
				@media (min-width: 771px) {
					border-top-right-radius: inherit;
					border-bottom-left-radius: 0;
				}
			}
		}
		// Image: Full
		&.wph-modal--image_left,
		&.wph-modal--image_right {
			.wph-modal--image_full,
			.wph-modal--image_full img {
				border-radius: inherit;
			}
		}
	}
}