.wph-social-sharing .wph-sshare--design_three {
	a.native-social-share {
		.wph-social {
			width: auto;
			max-width: 28px;
			height: auto;
			max-height: 28px;
			border-radius: 30px;
		}
	}
	&.wph-sshare--count_inline {
		a.native-social-share {
			height: 30px;
			display: flex;
			flex-wrap: wrap;
			flex-direction: row;
			align-items: center;
			border-radius: 30px;
			.wph-social {
				margin: 0;
			}
			.wph-sshare_social_counter {
				min-width: 28px;
				padding: 0 8px;
			}
		}
	}
	&.wph-sshare--count_block {
		a.native-social-share {
			position: relative;
			border-radius: 30px;
			.wph-social {
				display: block;
				margin: 0 auto;
			}
			.wph-sshare_social_counter {
				display: block;
				padding: 6px 0 8px;
			}
		}
	}
}