.wph-modal.wph-modal--magic_bar {
	position: fixed;
	top: 0;
	right: 0;
	left: 0;
	// Style: Cabriolet
	&.wph-modal--cabriolet {
		header {
			padding: 20px 20px 0;
		}
		section {
			border-radius: 0;
			.wph-modal--image_full {
				img {
					width: 100%;
					max-height: none;
				}
			}
		}
	}
	// Style: Minimal
	&.wph-modal--minimal {
		.wph-modal--content {
			border-radius: 0;
			.wph-modal--image_full {
				img {
					width: 100%;
					max-height: none;
				}
			}
		}
	}
	// Style: Simple
	&.wph-modal--simple {
		.wph-modal--content {
			border-radius: 0;
			.wph-modal--image_full {
				img {
					width: 100%;
					max-height: none;
				}
			}
		}
	}
}