.wph-modal.wph-modal--after_content {
	width: 100%;
	max-width: 800px;
	visibility: hidden;
	backface-visibility: hidden;
	position: relative;
	margin: 0 auto;
	&.wph-modal-show {
		visibility: visible;
	}
	@media (max-width: 860px){
		padding: 0 30px;
	}
	@media (max-width: 770px){
		padding: 0 20px;
	}
}

.wph-modal.wph-modal--after_content {
	
	// Animation 1: Fade in and scale up
	&.fadein {
		opacity: 0;
		transform: scale(0.7);
		transition: all 0.3s;
		&.wph-modal-show {
			opacity: 1;
			transform: scale(1);
		}
	}
	
	// Animation 2: Slide from the right
	&.slideright {
		opacity: 0;
		transform: translateX(20%);
		transition: all 0.3s cubic-bezier(0.25, 0.5, 0.5, 0.9);
		&.wph-modal-show {
			opacity: 1;
			transform: translateX(0);
		}
	}
	
	// Animation 3: Slide from the bottom
	&.slidebottom {
		opacity: 0;
		transform: translate(0,20%);
		transition: all 0.3s;
		&.wph-modal-show {
			opacity: 1;
			transform: translate(0,0);
		}
	}
	
	// Animation 4: Newspaper
	&.newspaper {
		opacity: 0;
		transform: scale(0) rotate(720deg);
		transition: all 0.5s;
		&.wph-modal-show {
			opacity: 1;
			transform: scale(1) rotate(0deg);
		}
	}
	
	// Animation 5: Fall
	&.fall {
		perspective: 1300px;
		.wph-modal--content {
			opacity: 0;
			transform: translateZ(600px) rotateX(20deg);
			transform-style: preserve-3d;
		}
		.wph-modal--close {
			opacity: 0;
			transition: 0.5s ease-in;
		}
		&.wph-modal-show .wph-modal--content {
			opacity: 1;
			transform: translateZ(0px) rotateX(0deg);
			transition: all 0.3s ease-in;
		}
		&.wph-modal-show .wph-modal--close {
			opacity: 1;
		}
	}
	
	// Animation 6: Side Fall
	&.sidefall {
		perspective: 1300px;
		.wph-modal--content {
			opacity: 0;
			transform: translate(30%) translateZ(600px) rotate(10deg);
			transform-style: preserve-3d;
		}
		.wph-modal--close {
			opacity: 0;
			transition: 0.5s ease-in;
		}
		&.wph-modal-show .wph-modal--content {
			opacity: 1;
			transform: translate(0%) translateZ(0) rotate(0deg);
			transition: all 0.3s ease-in;
		}
		&.wph-modal-show .wph-modal--close {
			opacity: 1;
		}
	}
	
	// Animation 7: 3D Flip (Horizontal)
	&.flipx {
		perspective: 1300px;
		.wph-modal--content {
			opacity: 0;
			transform: rotateY(-70deg);
			transform-style: preserve-3d;
			transition: all 0.3s;
		}
		.wph-modal--close {
			opacity: 0;
			transition: 0.5s ease-in;
		}
		&.wph-modal-show .wph-modal--content {
			opacity: 1;
			transform: rotateY(0deg);
		}
		&.wph-modal-show .wph-modal--close {
			opacity: 1;
		}
	}
	
	// Animation 8: 3D Flip (Vertical)
	&.flipy {
		perspective: 1300px;
		.wph-modal--content {
			opacity: 0;
			transform: rotateX(-70deg);
			transform-style: preserve-3d;
			transition: all 0.3s;
		}
		.wph-modal--close {
			opacity: 0;
			transition: 0.5s ease-in;
		}
		&.wph-modal-show .wph-modal--content {
			opacity: 1;
			transform: rotateX(0deg);
		}
		&.wph-modal-show .wph-modal--close {
			opacity: 1;
		}
	}
	
	// Animation 9: 3D Sign
	&.sign {
		perspective: 1300px;
		.wph-modal--content {
			opacity: 0;
			transform: rotateX(-60deg);
			transform-style: preserve-3d;
			transition: all 0.3s;
		}
		.wph-modal--close {
			opacity: 0;
			transition: 0.5s ease-in;
		}
		&.wph-modal-show .wph-modal--content {
			opacity: 1;
			transform: rotateX(0deg);
		}
		&.wph-modal-show .wph-modal--close {
			opacity: 1;
		}
	}
	
	// Animation 10: Super Scaled
	&.scaled {
		opacity: 0;
		transform: scale(2);
		transition: all 0.3s;
		&.wph-modal-show {
			opacity: 1;
			transform: scale(1);
		}
	}
	
	// Animation 11: 3D Slit
	@keyframes slit {
		50% {
			opacity: 1;
			transform: translateZ(-250px) rotateY(89deg);
			animation-timing-function: ease-in;
		}
		100% {
			opacity: 1;
			transform: translateZ(0) rotateY(0deg);
		}
	}
	
	&.slit {
		perspective: 1300px;
		.wph-modal--content {
			opacity: 0;
			transform: translateZ(-3000px) rotateY(90deg);
			transform-style: preserve-3d;
		}
		.wph-modal--close {
			opacity: 0;
			transition: 0.5s ease-in;
		}
		&.wph-modal-show .wph-modal--content {
			animation: slit .7s forwards ease-out;
		}
		&.wph-modal-show .wph-modal--close {
			opacity: 1;
		}
	}
	
	// Animation 12: 3D Rotate (Bottom)
	&.rotatey {
		perspective: 1300px;
		.wph-modal--content {
			opacity: 0;
			transform: translateY(100%) rotateX(90deg);
			transform-origin: 0 100%;
			transform-style: preserve-3d;
			transition: all 0.3s ease-out;
		}
		.wph-modal--close {
			opacity: 0;
			transition: 0.5s ease-in;
		}
		&.wph-modal-show .wph-modal--content {
			opacity: 1;
			transform: translateY(0%) rotateX(0deg);
		}
		&.wph-modal-show .wph-modal--close {
			opacity: 1;
		}
	}
	
	// Animation 13: 3D Rotate (Left)
	&.rotatex {
		perspective: 1300px;
		.wph-modal--content {
			opacity: 0;
			transform: translateZ(100px) translateX(-30%) rotateY(90deg);
			transform-origin: 0 100%;
			transform-style: preserve-3d;
			transition: all 0.3s ease-out;
		}
		.wph-modal--close {
			opacity: 0;
			transition: 0.5s ease-in;
		}
		&.wph-modal-show .wph-modal--content {
			opacity: 1;
			transform: translateZ(0px) translateX(0%) rotateY(0deg);
		}
		&.wph-modal-show .wph-modal--close {
			opacity: 1;
		}
	}
	
}