.wph-modal.wph-modal--widget {
	width: 100%;
	max-width: 800px;
	visibility: visible;
	backface-visibility: hidden;
	position: relative;
	margin: 30px auto;
	&.wph-modal--small {
		// Style: Cabriolet
		&.wph-modal--cabriolet {
			.wph-modal--image_left,
			.wph-modal--image_right {
				.wph-modal--image {
					flex: 0 200px;
					img {
						width: 100%;
						height: auto;
						max-height: 200px;
					}
				}
			}
			.wph-modal--image_left {
				flex-direction: column;
				.wph-modal--image,
				.wph-modal--image img {
					border-bottom-left-radius: 0;
				}
			}
			.wph-modal--image_right {
				flex-direction: column-reverse;
				.wph-modal--image,
				.wph-modal--image img {
					border-top-right-radius: 0;
				}
			}
		}
		// Style: Simple
		&.wph-modal--simple {
			.wph-modal--content {
				flex-direction: column;
				&.wph-modal--image_right {
					flex-direction: column-reverse;
					.wph-modal--image,
					.wph-modal--image img {
						border-top-right-radius: 0;
					}
				}
				.wph-modal--image {
					flex: 0 200px;
					border-bottom-left-radius: 0;
					img {
						width: 100%;
						height: auto;
						max-height: 200px;
						border-bottom-left-radius: 0;
					}
				}
			}
		}
		// Style: Minimal
		&.wph-modal--minimal {
			.wph-modal--image_left,
			.wph-modal--image_right {
				.wph-modal--image {
					flex: 0 200px;
					img {
						width: 100%;
						height: auto;
						max-height: 200px;
					}
				}
			}
			.wph-modal--image_left {
				flex-direction: column;
				.wph-modal--image,
				.wph-modal--image img {
					border-bottom-left-radius: 0;
				}
			}
			.wph-modal--image_right {
				flex-direction: column-reverse;
				.wph-modal--image,
				.wph-modal--image img {
					border-top-right-radius: 0;
				}
			}
		}
	}
	@media (max-width: 860px){
		padding: 0 30px;
	}
	@media (max-width: 770px){
		padding: 0 20px;
	}
}