.wph-social-sharing .wph-sshare--design_one {
	a.native-social-share {
		padding: 8px 4px;
		background: transparent !important;
		.wph-social-path {
			width: auto;
			max-width: 20px;
			height: auto;
			max-height: 20px;
		}
	}
	&.wph-sshare--count_inline {
		a.native-social-share {
			height: 38px;
			display: flex;
			flex-wrap: wrap;
			flex-direction: row;
			align-items: center;
			.wph-social-path {
				margin: 0;
			}
			.wph-sshare_social_counter {
				margin: 0 8px 0 12px;
			}
		}
	}
	&.wph-sshare--count_block {
		a.native-social-share {
			position: relative;
			.wph-social-path {
				display: block;
				margin: 0 auto;
			}
			.wph-sshare_social_counter {
				display: block;
				padding: 6px 0 0;
			}
		}
	}
	&.wph-sshare--row {
		a.native-social-share {
			padding: 0 8px;
		}
		&.wph-sshare--count_inline {
			a.native-social-share {
				.wph-sshare_social_counter {
					margin-right: 0;
				}
			}
		}
		&.wph-sshare--count_block {
			a.native-social-share {
				.wph-sshare_social_counter {
					padding: 8px 0 0;
				}
			}
		}
	}
}