$hustle-font-path: "../icons" !default;

@font-face {
	font-family: 'hustle';
	src:  url('#{$hustle-font-path}/hustle.eot?xh498y');
	src:  url('#{$hustle-font-path}/hustle.eot?xh498y#iefix') format('embedded-opentype'),
		  url('#{$hustle-font-path}/hustle.ttf?xh498y') format('truetype'),
		  url('#{$hustle-font-path}/hustle.woff?xh498y') format('woff'),
		  url('#{$hustle-font-path}/hustle.svg?xh498y#hustle') format('svg');
	font-weight: normal;
	font-style: normal;
}

.wph-icon {
	// use !important to prevent issues with browser extensions that change fonts
	font-family: 'hustle' !important;
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;
	// Better Font Rendering
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

