.wph-modal .gform_wrapper {
	width: 100%;
	display: block;
	form .gform_heading {
		.gform_description {
			width: 100%;
			display: block;
			margin: 0;
		}
	}
	form .gform_body {
		width: 100%;
		ul.gform_fields {
			list-style: none;
			li {
				padding: 0;
			}
			li:before {
				display: none;
			}
		}
	}
}