.wph-modal.wph-modal--slide_in {
	max-width: 600px;
	opacity: 0;
	visibility: hidden;
	backface-visibility: hidden;
	position: fixed;
	transition: all 0.3s;
	&.wph-modal-show {
		opacity: 1;
		visibility: visible;
	}
	.wph-modal--close {
		margin-right: 10px;
	}
	.wph-modal--hide {
		display: none;
	}
	// Position: Top Left
	&.wph-slide--top_left {
		top: 0;
		left: 0;
		transform: translateY(-20%);
		&.wph-modal-show {
			transform: translateY(0);
		}
	}
	// Position: Top Center
	&.wph-slide--top_center {
		top: 0;
		left: 50%;
		transform: translate(-50%,-20%);
		&.wph-modal-show {
			transform: translate(-50%,0);
		}
	}
	// Position: Top Right
	&.wph-slide--top_right {
		top: 0;
		right: 0;
		transform: translateY(-20%);
		&.wph-modal-show {
			transform: translateY(0);
		}
	}
	// Position: Bottom Left
	&.wph-slide--bottom_left {
		bottom: 0;
		left: 0;
		transform: translateY(20%);
		&.wph-modal-show {
			transform: translateY(0);
		}
	}
	// Position: Bottom Center
	&.wph-slide--bottom_center {
		bottom: 0;
		left: 50%;
		transform: translate(-50%,20%);
		&.wph-modal-show {
			transform: translate(-50%,0);
		}
	}
	// Position: Bottom Right
	&.wph-slide--bottom_right {
		bottom: 0;
		right: 0;
		transform: translateY(20%);
		&.wph-modal-show {
			transform: translateY(0);
		}
	}
	// Position: Center Left
	&.wph-slide--center_left {
		top: 50%;
		left: 0;
		transform: translate(-20%,-50%);
		transition: all 0.3s cubic-bezier(0.25, 0.5, 0.5, 0.9);
		&.wph-modal-show {
			transform: translate(0,-50%);
		}
	}
	// Position: Center Right
	&.wph-slide--center_right {
		top: 50%;
		right: 0;
		transform: translate(20%,-50%);
		transition: all 0.3s cubic-bezier(0.25, 0.5, 0.5, 0.9);
		&.wph-modal-show {
			transform: translate(0,-50%);
		}
	}
	// Style: Cabriolet
	&.wph-modal--cabriolet {
		&.wph-slide--top_left section {
			@media (min-width: 770px) {
				border-top-left-radius: 0;
				border-top-right-radius: 0;
				border-bottom-left-radius: 0;
			}
		}
		&.wph-slide--top_center section {
			@media (min-width: 770px) {
				border-top-left-radius: 0;
				border-top-right-radius: 0;
			}
		}
		&.wph-slide--top_right section {
			@media (min-width: 770px) {
				border-top-left-radius: 0;
				border-top-right-radius: 0;
				border-bottom-right-radius: 0;
			}
		}
		&.wph-slide--bottom_left section {
			@media (min-width: 770px) {
				border-top-left-radius: 0;
				border-bottom-right-radius: 0;
				border-bottom-left-radius: 0;
			}
		}
		&.wph-slide--bottom_center section {
			@media (min-width: 770px) {
				border-bottom-right-radius: 0;
				border-bottom-left-radius: 0;
			}
		}
		&.wph-slide--bottom_right section {
			@media (min-width: 770px) {
				border-top-right-radius: 0;
				border-bottom-right-radius: 0;
				border-bottom-left-radius: 0;
			}
		}
		&.wph-slide--center_left section {
			@media (min-width: 770px) {
				border-top-left-radius: 0;
				border-bottom-left-radius: 0;
			}
		}
		&.wph-slide--center_right section {
			@media (min-width: 770px) {
				border-top-right-radius: 0;
				border-bottom-right-radius: 0;
			}
		}
	}
	// Style: Minimal
	&.wph-modal--minimal {
		&.wph-slide--top_left .wph-modal--content {
			@media (min-width: 770px) {
				border-top-left-radius: 0;
				border-top-right-radius: 0;
				border-bottom-left-radius: 0;
			}
		}
		&.wph-slide--top_center .wph-modal--content {
			@media (min-width: 770px) {
				border-top-left-radius: 0;
				border-top-right-radius: 0;
			}
		}
		&.wph-slide--top_right .wph-modal--content {
			@media (min-width: 770px) {
				border-top-left-radius: 0;
				border-top-right-radius: 0;
				border-bottom-right-radius: 0;
			}
		}
		&.wph-slide--bottom_left .wph-modal--content {
			@media (min-width: 770px) {
				border-top-left-radius: 0;
				border-bottom-right-radius: 0;
				border-bottom-left-radius: 0;
			}
		}
		&.wph-slide--bottom_center .wph-modal--content {
			@media (min-width: 770px) {
				border-bottom-right-radius: 0;
				border-bottom-left-radius: 0;
			}
		}
		&.wph-slide--bottom_right .wph-modal--content {
			@media (min-width: 770px) {
				border-top-right-radius: 0;
				border-bottom-right-radius: 0;
				border-bottom-left-radius: 0;
			}
		}
		&.wph-slide--center_left .wph-modal--content {
			@media (min-width: 770px) {
				border-top-left-radius: 0;
				border-bottom-left-radius: 0;
			}
		}
		&.wph-slide--center_right .wph-modal--content {
			@media (min-width: 770px) {
				border-top-right-radius: 0;
				border-bottom-right-radius: 0;
			}
		}
	}
	// Style: Simple
	&.wph-modal--simple {
		&.wph-slide--top_left .wph-modal--content {
			@media (min-width: 770px) {
				border-top-left-radius: 0;
				border-top-right-radius: 0;
				border-bottom-left-radius: 0;
			}
		}
		&.wph-slide--top_center .wph-modal--content {
			@media (min-width: 770px) {
				border-top-left-radius: 0;
				border-top-right-radius: 0;
			}
		}
		&.wph-slide--top_right .wph-modal--content {
			@media (min-width: 770px) {
				border-top-left-radius: 0;
				border-top-right-radius: 0;
				border-bottom-right-radius: 0;
			}
		}
		&.wph-slide--bottom_left .wph-modal--content {
			@media (min-width: 770px) {
				border-top-left-radius: 0;
				border-bottom-right-radius: 0;
				border-bottom-left-radius: 0;
			}
		}
		&.wph-slide--bottom_center .wph-modal--content {
			@media (min-width: 770px) {
				border-bottom-right-radius: 0;
				border-bottom-left-radius: 0;
			}
		}
		&.wph-slide--bottom_right .wph-modal--content {
			@media (min-width: 770px) {
				border-top-right-radius: 0;
				border-bottom-right-radius: 0;
				border-bottom-left-radius: 0;
			}
		}
		&.wph-slide--center_left .wph-modal--content {
			@media (min-width: 770px) {
				border-top-left-radius: 0;
				border-bottom-left-radius: 0;
			}
		}
		&.wph-slide--center_right .wph-modal--content {
			@media (min-width: 770px) {
				border-top-right-radius: 0;
				border-bottom-right-radius: 0;
			}
		}
	}
}
body.logged-in.home {
	.wph-modal.wph-modal--slide_in {
		margin-top: 32px;
		@media (max-width: 782px) {
			margin-top: 46px;
		}
	}
}